import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import { Link } from "react-router-dom";
import moment from "moment";

import notification from "../../../../components/notification";
import {
  Empty,
  Collapse,
  Select,
  Row,
  Col,
  Divider,
  DatePicker,
  TreeSelect
} from "antd";

// import Popconfirms from "../../../../components/feedback/popconfirm";
import IntlMessages from "../../../../components/utility/intlMessages";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import NumberFormat from "../../../App/NumberFormat";
import PageHeader from "../../../../components/utility/pageHeader";
import Scrollbars from "../../../../components/utility/customScrollBar";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/report/stockCardReport/actions";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import CardWrapper, {
  Box,
  // StatusTag,
  customCollapseStyle
  // StatusBadge,
  // TransNo
} from "../../../App/app.style";

import PrintWrapper from "../../../App/PrintWrapper";
import ReactToPrint from "react-to-print";
import ButtonPrint from "../../../App/ButtonPrint";
import ExcelWrapper from "../../../App/ExcelWrapper";
import PDFWrapper from "../../../App/PDFWrapper";
import getLanguage from "../../../App/MyLanguange";
import { getFileName } from "../../../../helpers/helper";
import { siteConfig } from "../../../../settings";

const Panel = Collapse.Panel;
const Option = Select.Option;
// const { RangePicker } = DatePicker;

const redirectTo = "/dashboard/report-menus";
const redirectItem = "/dashboard/setup/items";

class StockCardReport extends Component {
  state = {
    filter: {
      transaction_date: moment().format("YYYY-MM-DD HH:mm:ss"),
      id_location: null,
      id_item_category: null,
      stock_status: null,
      sort_by: "item_code",
      sort_type: "asc"
    }
  };

  // Get All data diawal
  componentDidMount() {
    const { getStockCardReports } = this.props;
    getStockCardReports();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.stockCardReport.error) {
      notification("error", this.props.stockCardReport.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.stockCardReport.success) {
      notification("success", this.props.stockCardReport.success);
      this.props.clearSuccess();
    }
  }

  //handle Search Value
  handleSearchValue = e => {
    const value = e.target.value;
    this.setState({ search: value });
  };

  //   Handle filter change
  handleFilterChange = (name, value) => {
    if (name === "transaction_date") {
      this.setState({
        filter: {
          ...this.state.filter,
          transaction_date: value.format("YYYY-MM-DD HH:mm:ss")
        }
      });
    } else {
      this.setState({
        filter: {
          ...this.state.filter,
          [name]: value
        }
      });
    }
  };

  filterValue = () => {
    const { filter } = this.state;
    let string = "";

    if (filter.transaction_date) {
      string += "transaction_date=" + filter.transaction_date;
    }
    if (filter.id_location) {
      string
        ? (string += "&id_location=" + filter.id_location)
        : (string += "id_location=" + filter.id_location);
    }
    if (filter.id_item_category) {
      string
        ? (string += "&id_item_category=" + filter.id_item_category)
        : (string += "id_item_category=" + filter.id_item_category);
    }
    if (filter.stock_status) {
      string
        ? (string += "&stock_status=" + filter.stock_status)
        : (string += "stock_status=" + filter.stock_status);
    }

    string
      ? (string +=
          "&sort_by=" + filter.sort_by + "&sort_type=" + filter.sort_type)
      : (string +=
          "sort_by=" + filter.sort_by + "&sort_type=" + filter.sort_type);

    return string;
  };

  // Handle filter
  handleFilter = () => {
    const { getStockCardReports } = this.props;
    //const { current_page } = this.props.stockCardReport;
    let filter = this.filterValue();

    this.setState({ search: null });
    getStockCardReports(1, null, filter, this.state.filter);
  };

  // Handle Reset Filter
  handleResetFilter = () => {
    const { getStockCardReports } = this.props;
    this.setState({
      filter: {
        transaction_date: moment().format("YYYY-MM-DD HH:mm:ss"),
        id_location: null,
        id_item_category: null,
        stock_status: null,
        sort_by: "item_code",
        sort_type: "asc"
      }
    });
    getStockCardReports();
  };

  render() {
    let columns = [
      {
        title: getLanguage({ id: "items.code" }),
        dataIndex: "item_code",
        rowKey: "item_code",
        width: "10%",
        render: (text, row) => (
          <Link to={`${redirectItem}/update/${row.id_item}`}>{text}</Link>
        )
      },
      {
        title: getLanguage({ id: "itemName" }),
        dataIndex: "item_name",
        rowKey: "item_name",
        width: "15%",
        render: text => <span>{text}</span>
      },
      {
        title: getLanguage({ id: "sidebar.itemCategory" }),
        dataIndex: "item_category_name",
        rowKey: "item_category_name",
        width: "15%",
        render: text => <span>{text}</span>
      },
      {
        title: getLanguage({ id: "minimumStock" }),
        dataIndex: "min_stock",
        rowKey: "min_stock",
        align: "right",
        width: "15%",
        parse: true,
        render: text => {
          return <NumberFormat value={parseFloat(text)} />;
        }
      },
      {
        title: getLanguage({ id: "maximumStock" }),
        dataIndex: "max_stock",
        rowKey: "max_stock",
        align: "right",
        width: "15%",
        parse: true,
        render: text => {
          return <NumberFormat value={parseFloat(text)} />;
        }
      },
      {
        title: getLanguage({ id: "reorderPoint" }),
        dataIndex: "reorder_point",
        rowKey: "reorder_point",
        align: "right",
        width: "15%",
        parse: true,
        render: text => {
          return <NumberFormat value={parseFloat(text)} />;
        }
      },
      {
        title: getLanguage({ id: "totalQtyOnHand" }),
        dataIndex: "total_qty_on_hand",
        rowKey: "total_qty_on_hand",
        align: "right",
        width: "15%",
        parse: true,
        render: text => {
          return (
            <strong>
              {" "}
              <NumberFormat value={parseFloat(text)} />{" "}
            </strong>
          );
        }
      },
      {
        title: getLanguage({ id: "sidebar.unit" }),
        dataIndex: "unit_code",
        rowKey: "unit_code",
        width: "5%",
        render: text => <span>{text}</span>
      }
    ];

    // Data setup
    const {
      stock_card_reports,
      locations,
      loading,
      loadingForeign,
      item_categories,
      filter,
      filter_select
    } = this.props.stockCardReport ? this.props.stockCardReport : [];

    let dataSource = [];

    stock_card_reports.map((data, index) => {
      return dataSource.push({ ...data, key: index });
    });

    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];

    const loop = array =>
      array.map(data => {
        let perData = {
          title: data.item_category_code + " (" + data.item_category_name + ")",
          value: data.id_item_category,
          key: data.id_item_category,
          children: data.children ? loop(data.children) : null
        };

        return perData;
      });

    const fileName = getLanguage({ id: "stockCardReports.pageHeader" });

    //console.log(this.props);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            title={<IntlMessages id="stockCardReports.pageHeader" />}
            marginBottom={true}
          >
            <PrintWrapper
              componentRef={ref => (this.componentRef = ref)}
              title={<IntlMessages id="stockCardReports.pageHeader" />}
              columns={columns}
              data={dataSource}
              filter={filter_select}
              filterDate="transaction_date"
              dateFormat="DD MMMM YYYY HH:mm:ss"
              locations={locations}
            />

            <ReactToPrint
              trigger={() => <ButtonPrint loading={loading} />}
              content={() => this.componentRef}
              onBeforePrint={() =>
                (document.title = getFileName(fileName, null, false))
              }
              onAfterPrint={() => (document.title = siteConfig.documentTitle)}
            />

            <PDFWrapper
              columns={columns}
              data={dataSource}
              fileName={fileName}
              filter={filter_select}
              loading={loading}
              locations={locations}
              scale={0.4}
              title="stockCardReports.pageHeader"
            />

            <ExcelWrapper
              title="stockCardReports.pageHeader"
              fileName={fileName}
              loading={loading}
              columns={columns}
              data={dataSource}
              filter={filter_select}
              filterDate="transaction_date"
              dateFormat="DD MMMM YYYY HH:mm:ss"
              locations={locations}
            />

            <Button
              style={{ marginLeft: "5px" }}
              onClick={() => this.props.history.replace(redirectTo)}
            >
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          <div className="isoFilter">
            <Collapse style={customCollapseStyle} defaultActiveKey={["1"]}>
              <Panel header={<IntlMessages id="filter.header" />} key="1">
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "10px" }}
                >
                  <Col span={12}>
                    <label>
                      <IntlMessages id="locations.pageHeader" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "id_location"
                      )}
                      value={
                        this.state.filter.id_location
                          ? this.state.filter.id_location
                          : []
                      }
                      style={{ width: "95%" }}
                      placeholder={<IntlMessages id="select.location" />}
                      optionFilterProp="children"
                      filterOption={true}
                      loading={loadingForeign}
                      //allowClear={true}
                      //showArrow={false}
                    >
                      {locations.map((data, key) => (
                        <Option key={key} value={data.id_location}>
                          {`${data.location_code} (${data.location_name})`}
                        </Option>
                      ))}
                    </Select>
                  </Col>
                  <Col span={12}>
                    <label>
                      <IntlMessages id="items.itemCategory" />
                    </label>
                    <br />

                    <TreeSelect
                      placeholder={
                        <IntlMessages id="items.select.itemCategory" />
                      }
                      onChange={this.handleFilterChange.bind(
                        this,
                        "id_item_category"
                      )}
                      style={{ width: "100%" }}
                      allowClear={true}
                      showSearch={true}
                      treeNodeFilterProp="title"
                      dropdownStyle={{ maxHeight: 300, overflow: "auto" }}
                      treeData={loop(item_categories)}
                      treeDefaultExpandAll
                      loading={loading}
                      value={
                        this.state.filter.id_item_category
                          ? this.state.filter.id_item_category
                          : []
                      }
                    />
                  </Col>
                </Row>
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "24px" }}
                >
                  <Col span={12}>
                    <Col span={12}>
                      <label>
                        <IntlMessages id="transactionDate" />
                      </label>
                      <br />
                      <DatePicker
                        style={{ width: "95%" }}
                        showTime
                        format="DD MMMM YYYY HH:mm:ss"
                        onChange={this.handleFilterChange.bind(
                          this,
                          "transaction_date"
                        )}
                        value={
                          this.state.filter.transaction_date
                            ? moment(this.state.filter.transaction_date)
                            : moment()
                        }
                        allowClear={false}
                        // disabledDate={e => e.valueOf() > moment()}
                      />
                    </Col>
                    <Col span={12}>
                      <label>
                        <IntlMessages id="stockStatus" />
                      </label>
                      <br />
                      <Select
                        showSearch
                        onChange={this.handleFilterChange.bind(
                          this,
                          "stock_status"
                        )}
                        value={
                          this.state.filter.stock_status
                            ? this.state.filter.stock_status
                            : []
                        }
                        style={{ width: "90%" }}
                        // placeholder={
                        //   <IntlMessages id="stockValuationReports.sortBy.select" />
                        // }
                        optionFilterProp="children"
                        filterOption={true}
                        allowClear={true}
                        //showArrow={false}
                      >
                        <Option key={1} value="<=min_stock">
                          <IntlMessages id="<=minStock" />
                        </Option>
                        <Option key={2} value="<=reorder_point">
                          <IntlMessages id="<=reorderPoint" />
                        </Option>
                        <Option key={3} value="<0">
                          {"<0"}
                        </Option>
                        <Option key={4} value=">0">
                          {">0"}
                        </Option>
                        <Option key={5} value=">=max_stock">
                          <IntlMessages id=">=maxStock" />
                        </Option>
                      </Select>
                    </Col>
                  </Col>
                  <Col span={12}>
                    <Row justify="start" gutter={1} type="flex">
                      <Col span={12}>
                        <label>
                          <IntlMessages id="sortBy" />
                        </label>
                        <br />
                        <Select
                          showSearch
                          onChange={this.handleFilterChange.bind(
                            this,
                            "sort_by"
                          )}
                          value={
                            this.state.filter.sort_by
                              ? this.state.filter.sort_by
                              : "item_code"
                          }
                          style={{ width: "95%" }}
                          // placeholder={
                          //   <IntlMessages id="stockValuationReports.sortBy.select" />
                          // }
                          optionFilterProp="children"
                          filterOption={true}
                          //allowClear={true}
                          //showArrow={false}
                        >
                          <Option key={1} value="item_code">
                            <IntlMessages id="items.code" />
                          </Option>
                          <Option key={2} value="item_name">
                            <IntlMessages id="itemName" />
                          </Option>
                          <Option key={3} value="total_qty_on_hand">
                            <IntlMessages id="totalQtyOnHand" />
                          </Option>
                        </Select>
                      </Col>
                      <Col span={12}>
                        <label>
                          <IntlMessages id="sortType" />
                        </label>
                        <br />
                        <Select
                          showSearch
                          onChange={this.handleFilterChange.bind(
                            this,
                            "sort_type"
                          )}
                          value={
                            this.state.filter.sort_type
                              ? this.state.filter.sort_type
                              : "asc"
                          }
                          style={{ width: "100%" }}
                          // placeholder={
                          //   <IntlMessages id="stockValuationReports.sortBy.select" />
                          // }
                          optionFilterProp="children"
                          filterOption={true}
                          //allowClear={true}
                          //showArrow={false}
                        >
                          <Option key={1} value="asc">
                            <IntlMessages id="ascending" />
                          </Option>
                          <Option key={2} value="desc">
                            <IntlMessages id="descending" />
                          </Option>
                        </Select>
                      </Col>
                    </Row>
                  </Col>
                </Row>
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "10px" }}
                >
                  <Col span={12}>
                    <Button
                      size="small"
                      type="primary"
                      style={{ marginRight: "10px" }}
                      onClick={this.handleFilter}
                      disabled={!this.state.filter.id_location}
                    >
                      <IntlMessages id="filter.only" />
                    </Button>
                    <Button onClick={this.handleResetFilter} size="small">
                      <IntlMessages id="filter.reset" />
                    </Button>
                  </Col>
                </Row>
              </Panel>
            </Collapse>
          </div>

          <CardWrapper>
            {!filter ? (
              <Empty description={<IntlMessages id="noLocation" />} />
            ) : !filter.includes("id_location=") ? (
              <Empty description={<IntlMessages id="noStockCard" />} />
            ) : dataSource.length === 0 &&
              !this.props.stockCardReport.loading ? (
              <Empty
                description={
                  <IntlMessages id="stockCardReports.table.noData" />
                }
              />
            ) : (
              <div className="isoInvoiceTable">
                <Divider orientation="left" style={{ fontSize: 14 }}>
                  <IntlMessages id="details" />
                </Divider>
                <Scrollbars style={{ width: "100%" }}>
                  <TableWrapper
                    dataSource={dataSource}
                    rowKey={record => record.id_item}
                    columns={columns}
                    loading={loading}
                    className="invoiceListTable"
                    bordered={true}
                    // Set pagination, jika ada page_size mengunakan pagination API
                    // jika tidak ada page_size menggunakan pagination bawaan
                    pagination={false}
                  />
                </Scrollbars>
              </div>
            )}
          </CardWrapper>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    stockCardReport: state.stockCardReport,
    auth: state.auth
  };
};

export default compose(connect(mapStateToProps, actions))(StockCardReport);
